import React,{Component} from 'react'
import './CouponItem.styl'
export default class CouponItem extends Component {
    render(){
        return (
            <div className='CouponItem'>
                <div className='kingaku'>
                    <p className='nedan ac'>9999</p>
                    <p className='yunitto ac'></p>
                </div>
                <div className='coupon-detail'>
                    <p>)</p>
                    <p>2019.01.14-2019.01.28</p>
                    <a href=""></a>
                </div>
            </div>
        )
    }
}



